import React, { Component } from 'react';

class Counter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 0
        };
    }
    increment = () => {
        this.setState({
            count: this.state.count + 1
        });
    }

    render(){
        return (
            <div>
              {this.state.count}  <i class="far fa-thumbs-up" onClick= {this.increment}></i>
            </div>
        )
    }
}

export default Counter